(function( $ ){
    $.fn.myPop = function(options) {
        try {
            this.each(function(x, dom) {

                if (dom.tagName !== 'INPUT') return

                var inputDom = $(this)
                var ph = inputDom.attr('placeholder')

                inputDom.css({
                    border: 'none',
                    height: '32px',
                    fontSize: '16px',
                    border: '1px solid #999',
                    outline: 'none',
                    padding: '0 10px',
                })

                var hintDom = null
                var hint= '<div style="background: #fff; color: #999;display: none; position: fixed; font-size: 12px;font-weight: bold; height: 20px">'+ ph +'</div>';


                inputDom.focus(function(){
                    inputDom.attr('placeholder', '');
                    hintDom = $(hint);
                    hintDom.css({
                        top: inputDom.offset().top - 10 - $(window).scrollTop(),
                        left: inputDom.offset().left + 5 - $(window).scrollLeft(),
                    })

                    inputDom.after(hintDom)
                    $(hintDom).slideDown(300)
                })

                function removeHintDom () {
                    hintDom && hintDom.slideUp(300).remove()
                }

                inputDom.blur(function(){
                    inputDom.attr('placeholder', ph);
                    removeHintDom()
                })

                $(window).scroll(function(event){
                    removeHintDom()
                });
            });
        } catch (e) {
            console.log('e :', e);
        }
    };
})( jQuery );
